.project_info_div {
	padding: 7% 15%;
}

.action_btns_div {
	display: flex;
	/* justify-content: space-around; */
	width: 50%;
}

.action_btn {
	width: 30%;
	text-align: center;
	font-size: 1rem;
	border: none;
	border-radius: 1rem;
	margin-right: 4%;
	padding: 2.5% 0;

	background-color: var(--social-bg-color);
	color: var(--social-link-color);
	text-decoration: none;
}

.action_btn:hover {
	background-color: var(--social-link-color);
	color: #d33a3a;
	cursor: pointer;
}

@media (min-width: 320px) and (max-width: 425px) {
	.project_info_div {
		padding: 20% 10%;
	}

	.action_btns_div {
		width: 100%;
	}

	.project_info_div button {
		width: 40%;
	}
}
